<template>
 <div>
   <draggable-table :data-source="data" :columns="columns" :replaceFields="{key: 'key'}" @draggable="handleDraggable">
     <template slot="customDrag" slot-scope="scope">
       {{scope.index}}
     </template>
   </draggable-table>
 </div>
</template>

<script>
import { DraggableTable } from '@handday/components'

const columns = [
  {
    title: '',
    width: 100,
    scopedSlots: { customRender: 'customDrag' }
  },
  {
    title: 'Name',
    dataIndex: 'name',
    width: 200,
  },
  {
    title: 'Age',
    dataIndex: 'age',
    width: 200,
  },
  {
    title: 'Address',
    dataIndex: 'address',
    width: 200,
  },
  {
    title: 'operation',
    dataIndex: 'operation',
    scopedSlots: { customRender: 'operation' },
  }
]

const data = [];
for (let i = 0; i < 46; i++) {
  data.push({
    key: i,
    name: `Edward King ${i}`,
    age: 32,
    address: `London, Park Lane no. ${i}`
  })
}

export default {
  name: 'TableDraggables',
  data() {
    return {
      data,
      columns,
      isDraggable: true
    }
  },
  components: {
    DraggableTable
  },
  methods: {
    handleDraggable(result, status) {
      console.log(result, status)
      this.data = result.data
    }
  }
}
</script>
